*,
html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "montserrat", sans-serif;
  background-color: #000;
  max-width: 100vw;
  overflow-x: hidden;
  position: absolute;
  width: 100vw;
}

h1,
h2,
p {
  margin: 0;
}

#loader {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  color: white;
  z-index: 10;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  h1 {
    font-size: 3rem;
  }
  span {
    font-family: "volunmo", sans-serif;
    font-size: 3rem;
  }
  #progress-bar {
    height: 4rem;
    border-radius: 0px;
    transform: skew(-40deg);
    max-width: 450px;
    width: 100%;
    &::-webkit-progress-bar {
      background-color: #0b3f23;
    }
    &::-webkit-progress-value {
      background-color: #25bc6a;
    }
  }
}

header {
  color: white;
  position: fixed;
  width: 100%;
}

header nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0 2rem;
}

header div {
  display: flex;
  flex-direction: row;
  gap: 5em;
}

h1 {
  font-size: 2rem;
  font-weight: 900;
}

p,
a {
  font-size: 1rem;
  color: white;
  text-decoration: none;
}

#container canvas {
  width: 100vw !important;
  height: 100vh !important;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -10;
}

h1,
h2,
h3 {
  font-family: "Darknet", sans-serif;
  color: white;
}

h2 {
  font-size: 2rem;
  font-weight: 900;
  color: #25bc6a;
}

p,
a,
li {
  font-family: "volunmo", sans-serif;
  font-size: 1.6rem;
}

@media (min-width: 1400px) {
  // h2 {
  //   font-size: 2rem;
  // }
  p {
    font-size: 1.6rem;
  }
}

@media (min-width: 1600px) {
  h2 {
    font-size: 2.2rem;
  }
  p {
    font-size: 1.8rem;
  }
}

button {
  background-color: #03170c;
  color: white;
  border: none;
  padding: 0 2rem 0.3rem 2rem;
  font-family: "volunmo", sans-serif;
  font-size: 2rem;
  align-self: center;
}

section.scrolling {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
